	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Pack Mule","date":"06 Jan 2002","slug":"/2002-01-06-pack-mule/","tags":"Y2002"},"html":"<p>PapaScott Headquarters is moving starting today... from the upstairs office to the basement guest room. The office is just too tight on space for both me and Mama to work and for Christopher to play at the same time. And a wireless card should enable us to continue to share our Internet connection... at least I don't think there's too much steel and concrete in the way. Otherwise <span style=\"text-decoration: line-through;\">Mama will have to do without,</span> er, I mean I'll have to run a cable to connect the rooms.</p>\n<p>Anyway, first step is to move 4 bookshelves with about 20 m of <a href=\"http://www.ora.com\">animal books</a> (and a few others) down 2 flights of stairs.</p>"},"next":{"fields":{"title":"Game Over","date":"05 Jan 2002","slug":"/2002-01-05-game-over/"}},"previous":{"fields":{"title":"U7","date":"07 Jan 2002","slug":"/2002-01-07-u7/"}}},{"node":{"fields":{"title":"Game Over","date":"05 Jan 2002","slug":"/2002-01-05-game-over/","tags":"Y2002"},"html":"<p>I spent my last DM note for lunch yesterday. The game is now over, the Euro has won, the DM is gone. Thanks for following the play by play the past few days.</p>"},"next":{"fields":{"title":"Big Mac Standard","date":"04 Jan 2002","slug":"/2002-01-04-big-mac-standard/"}},"previous":{"fields":{"title":"Pack Mule","date":"06 Jan 2002","slug":"/2002-01-06-pack-mule/"}}},{"node":{"fields":{"title":"Big Mac Standard","date":"04 Jan 2002","slug":"/2002-01-04-big-mac-standard/","tags":"Y2002"},"html":"<p>As if Mama doesn't have enough to do at work, now she also has to protect the price stability of the Euro. <a href=\"http://news.bbc.co.uk/hi/english/business/newsid_1739000/1739966.stm\">BBC</a>:<br />\n\"When I bought a Big Mac and a strawberry milkshake this week it cost 4.45 euros, which is exactly the same amount as I paid for the same meal last week.\" (said European Central Bank President Wim Duisenberg).</p>"},"next":{"fields":{"title":"Backwards Progress","date":"03 Jan 2002","slug":"/2002-01-03-backwards-progress/"}},"previous":{"fields":{"title":"Game Over","date":"05 Jan 2002","slug":"/2002-01-05-game-over/"}}}],"pathPrefix":"","first":false,"last":false,"index":952,"pageCount":1101}}

